.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  margin-top: 80px;
  margin-left: 50px;
  margin-right: 50px;
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.header{
  border: none;
  background-color: #212C58;
  text-align: center;
  padding: 10px;
  margin-left: 50px;
  margin-right: 50px;
}

.aside{
  columns: 3;
  column-gap: 1em;
  padding: 10px 50px 20px;
  text-align: center;
  font-size: 20px;
}
.aside1{
  background-color: #212C58;
  padding: 120px;
}
.aside2{
  background-color: #212C58;
  padding: 120px;
}
.aside3{
  background-color: #212C58;
  padding: 120px;
}

.footer{
  border: none;
  background-color: #212C58;
  text-align: center;
  padding: 10px;
  margin-left: 50px;
  margin-right: 50px;

}

.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #212C58;
  padding: 10px;
  margin-left: 50px;
  margin-right: 50px;
}

.mundo{
  color: black;
  font-size: 40px;
  text-align: center;
  font-family: 'Times New Roman', Times, serif;
}
.section{
  align-items: center;
  background-color: #212C58;
  text-align: center;
}
.section ul{
  list-style: none;
  margin-left: 250px;
  display: flex;
}
.section li{
  text-align: center;
  margin: 40px;
}
.section a{
  text-decoration: none;
  color: white;
}